		getCalcExamFileList(fileExtName) {
			if (undefined == fileExtName || null == fileExtName || "" == fileExtName) {
				this.form.curCalcExamFileList = [];
				return;
			}
			let that = this;
			var fileExt = "*." + fileExtName;
			//var fileExt = '["*.' + fileExtName + '","' + fileExtName + '*.dat"]';
			var fileListParams = '{"Cmd":"GetFilesFromDirectory","Dir":' + '\"' + that.workPath.replace(/\\/g, '\\\\') +
				'\"' + ', "Ext":' + '\"' + fileExt + '\"' + ',"FindSubDir":' + false + '}';
			try {
				var Obj = JSON.parse(that.form.curSelSoftware.app);
				if (Obj.hasOwnProperty('SubWorkPath')) {
					fileListParams = '{"Cmd":"GetFilesFromDirectory","Dir":' + '\"' + that.workPath.replace(/\\/g, '\\\\') + Obj.SubWorkPath.replace(/\\/g, '\\') +
						'\"' + ', "Ext":' + '\"' + fileExt + '\"' + ',"FindSubDir":' + false + '}';
				}
			}
			catch (error) {
				fileListParams = '{"Cmd":"GetFilesFromDirectory","Dir":' + '\"' + that.workPath.replace(/\\/g, '\\\\') + 'ToolBox\\\\' +
					that.form.curSelSoftware.fileExtName + '\\\\' + '\"' + ', "Ext":' + '\"' + fileExt + '\"' + ',"FindSubDir":' + false + '}';
			}

			//增加是否合并分支
			if (that.getFileListCombine) {
				var param = JSON.parse(fileListParams);
				param.ExistData = that.calcExamFileList;
				fileListParams = JSON.stringify(param);
			}
			localhostFunc(
				'{ "LocalFunc": { "FuncId": "501"}, "Param": ' + fileListParams + ' }',
				false,
				function (response) {
					that.getFileListCombine = false; //关闭合并标志
					that.calcExamFileList = JSON.parse(response);
					that.form.curCalcExamFileList = that.searchCalcExamFile(that.calcExamFileList, that.form.searchCalcExamKey);
				},
				function (code, msg) {
					console.log(code + "-" + msg);
				}
			);
		},

		searchCalcExamFile(fileList, searchKey) {
			var filterList = [];
			if (undefined == fileList || undefined == searchKey || "" == searchKey) {
				filterList = fileList;
			} else {
				for (var i = 0; i < fileList.length; ++i) {
					if (fileList[i].FileName.toLowerCase().indexOf(searchKey.toLowerCase()) > -1) {
						filterList.push(fileList[i]);
					}
				}
			}
			return filterList;
		},

