		// 取收藏列表
		getCollectionModuleList(filterPar, tokenid, aestokenid, filterCallback, failureCallback, authModuleIdCallback, authCallcack) {
			var urlPlat = JSON.parse(sessionStorage.getItem('serverMap')).cadsvrplatserver;
			if (undefined == tokenid || null == tokenid || "" == tokenid) {
				if (undefined != failureCallback && null != failureCallback) {
					failureCallback();
				}
			}
			else {
				var urlAddr = urlPlat + '/api/CADSvrPlat/UserCollection/GetCollectionList/0/' + tokenid;
				axios.post(urlAddr, qs.stringify(filterPar)).then(function (res) {
					// 响应成功回调
					if (undefined != res.data.DataContext) {
						if (undefined != filterCallback && null != filterCallback) {
							filterCallback(res.data.DataContext, tokenid, aestokenid);
						}

						if (undefined != authModuleIdCallback && null != authModuleIdCallback) {
							// 根据模块id请求模块是否可用api
							var urlPlat_ = JSON.parse(sessionStorage.getItem('serverMap')).cadcloudsoftwaremallserver;
							urlPlat_ += '/api/CADSoftwareMall/Authorization/CheckAuthorization/' + tokenid;
							// 批量获取模块授权状态
							var arrModuleId = authModuleIdCallback();
							var paramData = {
								"": arrModuleId
							};
							if (undefined != arrModuleId && null != arrModuleId && arrModuleId.length > 0) {
								return axios.post(urlPlat_, qs.stringify(paramData, { arrayFormat: 'repeat' }));
							}
						}
					}
				}).then(res => {
					if (undefined != res.data.DataContext && res.data.DataContext.length > 0) {
						if (undefined != authCallcack && null != authCallcack) {
							authCallcack(res.data.DataContext);
						}
					}
				}).catch(res => {
					if (!axios.isCancel(res)) {
						console.log(res);
					}
				});
			}
		},

